import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TableSortLabel from '@mui/material/TableSortLabel';
import Paper from '@mui/material/Paper';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import { visuallyHidden } from '@mui/utils';
import Link from '@mui/material/Link';
import nfts from './config';
import axios from 'axios';
import BigNumber from "bignumber.js";

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) {
      return order;
    }
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

const headCells = [
  {
    id: 'name',
    numeric: false,
    disablePadding: true,
    label: 'Collection',
  },
  {
    id: 'buyPrice',
    numeric: true,
    disablePadding: true,
    label: 'Buy Price'
  },
  {
    id: 'floorPrice',
    numeric: true,
    disablePadding: false,
    label: 'Floor Price',
  },
  {
    id: 'tokenPrice',
    numeric: true,
    disablePadding: false,
    label: 'Spot Price',
  },
  {
    id: 'rate',
    numeric: true,
    disablePadding: false,
    label: 'Rate',
  },
  {
    id: 'swap',
    numeric: false,
    disablePadding: false,
    label: 'Swap',
  },
];

function EnhancedTableHead(props) {
  const { order, orderBy, onRequestSort } =
    props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            align='center'
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              onClick={createSortHandler(headCell.id)}
            >
              {headCell.label}
              {orderBy === headCell.id ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.oneOf(['asc', 'desc']).isRequired,
  orderBy: PropTypes.string.isRequired,
};

function EnhancedTable(props) {
  const [order, setOrder] = React.useState('desc');
  const [orderBy, setOrderBy] = React.useState('rate');
  const [dense, setDense] = React.useState(true);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangeDense = (event) => {
    setDense(event.target.checked);
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Paper sx={{ width: '100%', mb: 2 }}>
        <TableContainer>
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size={dense ? 'small' : 'medium'}
          >
            <EnhancedTableHead
              order={order}
              orderBy={orderBy}
              onRequestSort={handleRequestSort}
            />
            <TableBody>
              {stableSort(props.data, getComparator(order, orderBy))
                .map((row, index) => {
                  const labelId = `enhanced-table-checkbox-${index}`;
                  return (
                    <TableRow
                      hover
                      tabIndex={-1}
                      key={row.name}
                    >
                      <TableCell
                        component="th"
                        id={labelId}
                        scope="row"
                        padding="none"
                        align="center"
                      >
                        <Link href={`https://opensea.io/collection/${row.name}/activity`} target="_blank">{row.name}</Link>&nbsp;&nbsp;&nbsp;
                        <Link href={`https://www.gem.xyz/collection/${row.name}`} target="_blank">gem</Link>&nbsp;&nbsp;&nbsp;
                        <Link href={`https://x2y2.io/collection/${row.name}`} target="_blank">X</Link>&nbsp;&nbsp;&nbsp;
                        <Link href={`https://looksrare.org/collections/${row.nftAddress}`} target="_blank">L</Link>&nbsp;&nbsp;&nbsp;
                        <Link href={`https://nftx.io/vault/${row.tokenAddress}/info/`} target="_blank">nftx</Link>
                        <Link href={`https://sudoswap.xyz/#/browse/sell/${row.nftAddress}`} target="_blank">sudo</Link>
                      </TableCell>
                      <TableCell align="center" 
                        sx={{
                          background: row.buyPrice>0?(row.buyPrice<row.floorPrice?"green":(row.buyPrice*0.95<row.floorPrice?"yellow":"")):""
                      }}> {row.buyPrice>0?row.buyPrice:""} </TableCell>
                      <TableCell align="center">{row.floorPrice}</TableCell>
                      <TableCell align="center"><Link href={`https://dextools.io/app/ether/pair-explorer/${row.pairAddress}`} target="_blank">{row.tokenPrice}</Link></TableCell>
                      <TableCell align="center">{row.rate}%</TableCell>
                      <TableCell align="center">
                        <Link href={`https://app.1inch.io/#/1/unified/swap/WETH/${row.tokenAddress}`} target="_blank">Buy</Link>&nbsp;&nbsp;&nbsp;
                        <Link href={`https://app.1inch.io/#/1/unified/swap/${row.tokenAddress}/WETH`} target="_blank">Sell</Link>
                      </TableCell>
                    </TableRow>
                  );
                })}

            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
      <FormControlLabel
        control={<Switch checked={dense} onChange={handleChangeDense} />}
        label="Dense padding"
      />
    </Box>
  );
}

export default function App(props) {

  const [time, setTime] = React.useState(0);
  const [data, setData] = React.useState(nfts.map((nft) => {
    return {
      name: nft[0],
      floorPrice: undefined,
      tokenPrice: undefined,
      rate: undefined,
      tokenAddress: nft[1],
      pairAddress: nft[2],
      buyPrice: undefined,
      nftAddress: nft[5],
    }
  }));
  const [rateCache, setRateCache] = React.useState([]);

  const WETH = "0xc02aaa39b223fe8d0a0e5c4f27ead9083c756cc2"
  let globalTimeout
  const gap = 60

  React.useEffect(() => {
    if (time) {
      if (time % 10 === 0) {
        refreshPrice()
      }
      globalTimeout = setTimeout(() => { setTime(time - 1) }, 1000)
    } else {
      refreshData()
    }
  }, [time])

  const refreshPrice = async () => {
    for (const i in nfts) {
      const fff =  Math.floor(Math.random()*2)
      const rpc = ["https://eth-mainnet.g.alchemy.com/v2/2kUVO1cQOxyE4ve84OHCcNdIMMdc7ryT", "https://mainnet.infura.io/v3/0c5b30c0df37450eabcefa904d8a008a"][fff]
      const givenRPC = window.location.search.substr(1)
      axios.post(givenRPC === '' ? rpc: givenRPC, {
        "jsonrpc": "2.0",
        "method": "eth_call",
        "params": [{ "to": nfts[i][2], "data": "0x0902f1ac" }, "latest"],
        "id": 1
      }).then(res => {
        const price0 = new BigNumber(res.data.result.substr(0, 66))
        const price1 = new BigNumber("0x" + res.data.result.substr(66, 64))
        const price = ((new BigNumber(WETH.toLocaleLowerCase())).lt(new BigNumber(nfts[i][1].toLocaleLowerCase())) ?
          price0.div(price1) : price1.div(price0)).toFixed(4)


        if (price !== data[i]['tokenPrice']) {

          const buyPrice = (new BigNumber(WETH.toLocaleLowerCase())).lt(new BigNumber(nfts[i][1].toLocaleLowerCase())) ?
            (price0.multipliedBy(x18(nfts[i][3]))) / (0.997 * (price1.minus(x18(nfts[i][3]))))
            :
            (price1.multipliedBy(x18(nfts[i][3]))) / (0.997 * (price0.minus(x18(nfts[i][3]))))

          const bp = f18(buyPrice)
          if (bp > price * 2) {
            data[i]['buyPrice'] = undefined
          } else {
            data[i]['buyPrice'] = bp
          }

          const before = data[i]['tokenPrice']
          data[i]['tokenPrice'] = price
          if (data[i]['floorPrice']) {
            const newRate = parseFloat(((data[i]['floorPrice'] / data[i]['tokenPrice'] - 1) * 100).toFixed(2))

            if (data[i]['rate']) {
              new Notification(nfts[i][0], { body: `Token price:${before} -> ${data[i]['tokenPrice']} \nRate:${data[i]['rate']} -> ${newRate}` })
            }

            data[i]['rate'] = newRate
          }
          setData(data)
        }
      })
    }
  }

  const refreshData = async () => {

    for (const i in nfts) {
      axios.get(`https://api.opensea.io/api/v1/collection/${nfts[i][0]}/stats`).then(res => {
        const before = data[i]['floorPrice']

        let nowFloorPrice = res.data.stats.floor_price
        if (nfts[i][0] === 'cryptopunks') {
          axios.get("https://api.nftgo.io/api/v1/collection-new/data/6113f83a3126bb611b9b117c/chart/price-V2?from=3655646851342&to=3655646851342").then(res => {

            if (data[i]['floorPrice'] !== res.data.data.floorPrice.meta.value) {
              data[i]['floorPrice'] = +(res.data.data.floorPrice.meta.value)
              if (data[i]['tokenPrice']) {
                const newRate = parseFloat(((data[i]['floorPrice'] / data[i]['tokenPrice'] - 1) * 100).toFixed(2))

                if (rateCache.indexOf(newRate) === -1) {
                  if (data[i]['rate']) {
                    new Notification(nfts[i][0], { body: `Floor price:${before} -> ${data[i]['floorPrice']} \nRate:${data[i]['rate']} -> ${newRate}` })
                  }
                  const cacheTmp = rateCache
                  cacheTmp.push(newRate)
                  setRateCache(cacheTmp)
                }

                data[i]['rate'] = newRate
              }
              setData(data)
            }

          })
        } else {
          if (data[i]['floorPrice'] !== nowFloorPrice) {
            data[i]['floorPrice'] = +(nowFloorPrice)
            if (data[i]['tokenPrice']) {
              const newRate = parseFloat(((data[i]['floorPrice'] / data[i]['tokenPrice'] - 1) * 100).toFixed(2))

              if (rateCache.indexOf(newRate) === -1) {
                if (data[i]['rate'] && Math.abs(newRate - data[i]['rate']) > 0.2) {
                  console.log(nfts[i][0], before, "->", data[i]['floorPrice'])
                  new Notification(nfts[i][0], { body: `Floor price:${before} -> ${data[i]['floorPrice']} \nRate:${data[i]['rate']} -> ${newRate}` })
                }
                const cacheTmp = rateCache
                cacheTmp.push(newRate)
                setRateCache(cacheTmp)
              }

              data[i]['rate'] = newRate
            }
            setData(data)
          }
        }
      })
    }

    setTime(gap)
    clearTimeout(globalTimeout)
  }

  if (window.location.href.indexOf("#1q2w3e") === -1) {
    return (<></>)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          marginBottom: "3vw",
          marginTop: "3vw",
        }}
      >
        <Button
          sx={{ textTransform: 'none' }}
          variant='contained' onClick={refreshData}
          disabled={time > gap - 3}
        >
          {time > gap - 3 ? "Refreshing" : `Refresh in ${time}s`}
        </Button>
        

      </Box>

      <EnhancedTable data={data}></EnhancedTable>
        
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          marginBottom: "3vw",
          marginTop: "3vw",
        }}
      >
        <Link href="https://coniun.io/new-mints" target="_blank">Mint now</Link>&nbsp;&nbsp;
        <Link href="https://etherscan.io/address/0xbe86f647b167567525ccaafcd6f881f1ee558216" target="_blank">New vault</Link>
      </Box>
    </Box>
  )
}

function x18(x) {
  return (new BigNumber(x)).multipliedBy((new BigNumber(10)).pow(18))
}

function f18(x) {
  return ((new BigNumber(x)).div((new BigNumber(10)).pow(18))).toNumber().toFixed(3)
}